html {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

label,
button {
  cursor: pointer;
}

.error {
  color: red;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.subtitle {
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 224px;
  margin-bottom: 20px;
}

.field {
  display: flex;
  justify-content: space-between;

  &__label {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
